<template>
  <div class="notFound" @click="click">
    <div class="base">
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {},
  methods: {
    click(e) {
      if (this.$route.path == "/") {
        // debugger
        var lastStep = this.step;
        if (e.pageY < window.innerHeight / 2) {
          if (this.step <= 1) {
            this.step++;
          }
        } else {
          if (this.step > 1 && this.step <= 3) {
            this.step++;
          }
        }
        if (this.step == lastStep) {
          if (e.pageX < window.innerWidth / 2) {
            if (this.step > 3 && this.step <= 5) {
              this.step++;
            }
          } else {
            if (this.step > 5 && this.step <= 7) {
              this.step++;
            }
          }
        }
        if (this.step == lastStep) {
          this.step = 0;
        } else if (this.step == 8) {
          this.$router.replace({ path: "/entry/views/index" });
        }
        console.log("click1", e.pageX, e.pageY, lastStep, this.step);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.notFound{
  width: 100vw;
  height: 100vh;
}

.base {
  height: 9em;
  left: 50%;
  margin: -7.5em;
  padding: 3em;
  position: absolute;
  top: 50%;
  width: 9em;
  transform: rotateX(45deg) rotateZ(45deg);
  transform-style: preserve-3d;
}

.cube,
.cube:after,
.cube:before {
  content: "";
  float: left;
  height: 3em;
  position: absolute;
  width: 3em;
}

/* Top */
.cube {
  background-color: #05afd1;
  position: relative;
  transform: translateZ(3em);
  transform-style: preserve-3d;
  transition: 0.25s;
  box-shadow: 13em 13em 1.5em rgba(0, 0, 0, 0.1);
  animation: anim 1s infinite;
}
.cube:after {
  background-color: #049dbc;
  transform: rotateX(-90deg) translateY(3em);
  transform-origin: 100% 100%;
}
.cube:before {
  background-color: #048ca7;
  transform: rotateY(90deg) translateX(3em);
  transform-origin: 100% 0;
}
.cube:nth-child(1) {
  animation-delay: 0.05s;
}
.cube:nth-child(2) {
  animation-delay: 0.1s;
}
.cube:nth-child(3) {
  animation-delay: 0.15s;
}
.cube:nth-child(4) {
  animation-delay: 0.2s;
}
.cube:nth-child(5) {
  animation-delay: 0.25s;
}
.cube:nth-child(6) {
  animation-delay: 0.3s;
}
.cube:nth-child(7) {
  animation-delay: 0.35s;
}
.cube:nth-child(8) {
  animation-delay: 0.4s;
}
.cube:nth-child(9) {
  animation-delay: 0.45s;
}

@keyframes anim {
  50% {
    transform: translateZ(0.5em);
  }
}
</style>
